import React, { useEffect } from 'react'
import { BrowserRouter, useLocation } from 'react-router-dom'

import './App.css'

import Navbar from './components/Navbar'

const App = () => {
	let location = useLocation()

	useEffect(() => {
		console.log('location changed App.js')
	}, [location])

	return (
		<BrowserRouter>
			<Navbar />
		</BrowserRouter>
	)
}

export default App
