import React, { Component } from 'react'
import { BrowserRouter as Router, useLocation } from 'react-router-dom'
import {
	MDBNavbar,
	MDBNavbarBrand,
	MDBNavbarNav,
	MDBNavbarToggler,
	MDBCollapse,
	MDBNavItem,
	MDBNavLink,
	MDBContainer
} from 'mdbreact'

import Routes from '../Routes'

class Navbar extends Component {
	constructor(props) {
		super(props)
		this.state = {
			collapse: false,
			isWideEnough: false,
			currentPath: '/'
		}
	}

	setCurrentPath = path => {
		this.setState({ currentPath: path })
	}

	onClick = () => {
		this.setState({
			collapse: !this.state.collapse
		})
	}

	render() {
		return (
			<div>
				<Router>
					<header>
						<MDBNavbar color='black' fixed='top' dark expand='md'>
							<MDBContainer>
								<MDBNavbarBrand href='/'>
									<strong>
										ENVID
										<span className='text-secondary'>STAT</span>
									</strong>
								</MDBNavbarBrand>
								<MDBNavbarToggler onClick={this.onClick} />
								<MDBCollapse isOpen={this.state.collapse} navbar>
									<MDBNavbarNav left>
										<MDBNavItem
											active={this.state.currentPath === '/'}
											onClick={() => this.setCurrentPath('/')}>
											<MDBNavLink to='/'>Home</MDBNavLink>
										</MDBNavItem>
										<MDBNavItem
											active={this.state.currentPath === '/maps'}
											onClick={() => this.setCurrentPath('/maps')}>
											<MDBNavLink to='/maps'>Maps</MDBNavLink>
										</MDBNavItem>
										<MDBNavItem
											active={this.state.currentPath === '/statistics'}
											onClick={() => this.setCurrentPath('/statistics')}>
											<MDBNavLink to='/statistics'>Statistics</MDBNavLink>
										</MDBNavItem>
										<MDBNavItem
											active={this.state.currentPath === '/about'}
											onClick={() => this.setCurrentPath('/about')}>
											<MDBNavLink to='/about'>About</MDBNavLink>
										</MDBNavItem>
									</MDBNavbarNav>
								</MDBCollapse>
							</MDBContainer>
						</MDBNavbar>
					</header>

					<div className='mt-5 pt-2'>
						<Routes />
					</div>
				</Router>
			</div>
		)
	}
}

export default Navbar
