import React from 'react'
import {
	MDBCard,
	MDBCardBody,
	MDBCardImage,
	MDBCardTitle,
	MDBCardText,
	MDBRow,
	MDBCol,
	MDBIcon,
	MDBContainer
} from 'mdbreact'

import ContributorCard from './ContributorCard'

const About = () => {
	return (
		<MDBContainer className='mt-4'>
			<MDBRow>
				<MDBCol>
					<h2 className='text-center font-weight-bold'>Contributors</h2>
				</MDBCol>
			</MDBRow>
			<MDBRow>
				<ContributorCard />
				<ContributorCard />
				<ContributorCard />
				<ContributorCard />
			</MDBRow>
		</MDBContainer>
	)
}

export default About
