import React from 'react'
import {
	MDBCard,
	MDBCardBody,
	MDBCardImage,
	MDBCardTitle,
	MDBCardText,
	MDBRow,
	MDBCol,
	MDBIcon
} from 'mdbreact'

const ContributorCard = props => {
	return (
		<MDBCol md='4' className="my-3">
			<MDBCard>
				<MDBCardImage
					hover
					overlay='white-light'
					className='card-img-top'
					src='https://mdbootstrap.com/img/Photos/Others/men.jpg'
					alt='man'
				/>

				<MDBCardBody cascade className='text-center'>
					<MDBCardTitle className='card-title'>
						<strong>Lorem ipsum</strong>
					</MDBCardTitle>

					<p className='font-weight-bold blue-text'>Wev developer</p>

					<MDBCardText>
						Sed ut perspiciatis unde omnis iste natus sit voluptatem accusantium
						doloremque laudantium, totam rem aperiam.{' '}
					</MDBCardText>

					<MDBCol md='12' className='d-flex justify-content-center'>
						<a href='!#' className='px-2 fa-lg li-ic'>
							<MDBIcon fab icon='linkedin-in'></MDBIcon>
						</a>

						<a href='!#' className='px-2 fa-lg tw-ic'>
							<MDBIcon fab icon='twitter'></MDBIcon>
						</a>

						<a href='!#' className='px-2 fa-lg fb-ic'>
							<MDBIcon fab icon='facebook-f'></MDBIcon>
						</a>
					</MDBCol>
				</MDBCardBody>
			</MDBCard>
		</MDBCol>
	)
}

export default ContributorCard
